export default class Animal{
    constructor(id,nome,dtNascimento,especie){
        this.id = id;
        this.nome = nome;
        this.dtNascimento = dtNascimento;
        this.especie = especie;
    }
    getId(){
        return this.id;
    }
    getNome(){
        return this.nome;
    }
    getDataNascimento(){
        return this.dtNascimento;
    }
    getEspecie(){
        return this.especie;
    }
}