import React,{Component} from 'react';

import BotaoSubmit from '../../componentes/BotaoSubmit';
import MenuSuperior from '../../componentes/MenuSuperior';

export default class FormularioConsulta extends Component{
    render(){
        return(
            <div>
                <MenuSuperior></MenuSuperior>
                <div className="container">
                    <form>
                        <div>
                            <h3>Realizar Consulta:</h3>
                        </div>
                        <div className="row form-group">
                            <div className="col-md-3">
                                <label>Nome do Animal:</label>
                                <select className="form-control">
                                    <option>Selecione...</option>
                                    <option>Glória</option>
                                    <option>Juzz</option>
                                </select>
                            </div>
                            <div className="col-md-3">
                                <label>Nome do Funcion&aacute;rio:</label>
                                <select className="form-control">
                                    <option>Selecione...</option>
                                    <option>Miraceli</option>
                                    <option>R&ocirc;mulo</option>
                                </select>
                            </div>
                            <div className="col-md-3">
                                <label>Data da Consulta:</label>
                                <input type="date" className="form-control"/>
                            </div>
                        </div>
                        <div className="row form-group">
                            <div className="col-md-6">
                                <label>Diagn&oacute;stico:</label>
                                <textarea className="form-control"></textarea>
                            </div>
                        </div>
                        <div className="row form-group">
                            <div className="col-md-3">
                                <BotaoSubmit/>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}