import React,{Component} from 'react';

import BotaoSubmit from '../../componentes/BotaoSubmit';
import MenuSuperior from '../../componentes/MenuSuperior';

export default class FormularioEspecie extends Component{
    render(){
        return(
            <div>
                <MenuSuperior></MenuSuperior>
                <div className="container">
                    <form>
                        <div>
                            <h3>Cadastro de Esp&eacute;cie:</h3>
                        </div>
                        <div className="row form-group">
                            <div className="col-md-6">
                                <label>Nome da Esp&eacute;cie:</label>
                                <input type="text" name="nomeAnimal" className="form-control"/>
                            </div>
                            <div className="col-md-3">
                                <label>Expectativa de Vida:</label>
                                <input type="text" name="dtNascAnimal" className="form-control"/>
                            </div>
                        </div>
                        <div className="row form-group">
                            <div className="col-md-3">
                                <BotaoSubmit/>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}