import React,{Component} from 'react';
import {Link} from 'react-router-dom';

export default class MenuSuperior extends Component{
    render(){
        return (
            <nav className="navbar navbar-expand-lg navbar-light bg-light">          
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">
                        <li className="nav-item">
                            <Link to="/" className="nav-link">Home</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/animais" className="nav-link">Animais</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/consultas" className="nav-link">Consultas</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/especies" className="nav-link">Especies</Link>
                        </li>
                        <li className="nav-item">
                            <Link to="/funcionarios" className="nav-link">Funcionarios</Link>
                        </li>
                    </ul>
                </div>
            </nav>
        );
    }
}