export default class HttpService{
    constructor(baseUrl){
        this.baseUrl = baseUrl;
    }

    handleRequest(res){
        if (!res.ok) {
            console.log(res);
            throw new Error('Ocorrey um erro ao executar requisição');
            return;
        }

        return res.json();
    }
    get(url){
        return fetch(this.baseUrl+url).then(this.handleRequest);
    }
}