import React from 'react';
import { BrowserRouter,Route, Switch } from 'react-router-dom';

import FormularioAnimal from './pages/FormularioAnimal';
import FormularioEspecie from './pages/FormularioEspecie';
import FormularioConsulta from './pages/FormularioConsulta';
import FormularioFuncionario from './pages/FormularioFuncionario';
import Main from './pages/Main';

const Routes = () => (
    <BrowserRouter basename={'/Trabalho_Ban2CrudWeb'}>
        <Switch>
            <Route path="/" exact component={Main}></Route>
            <Route path="/animais" component={FormularioAnimal}></Route>
            <Route path="/especies" component={FormularioEspecie}></Route>
            <Route path="/consultas" component={FormularioConsulta}></Route>
            <Route path="/funcionarios" component={FormularioFuncionario}></Route>
        </Switch>
    </BrowserRouter>
)
export default Routes;