import React,{Component} from 'react';

import BotaoSubmit from '../../componentes/BotaoSubmit';
import BotaoNovo from '../../componentes/BotaoNovo';
import MenuSuperior from '../../componentes/MenuSuperior';
import animalService from '../../services/AnimalService';
import FormMode from '../../enums/FormMode';

import {MdEdit,MdDelete} from 'react-icons/md';
import Animal from '../../models/Animal';


export default class FormularioAnimal extends Component{
    state = {
        animais:[],
        formControls:{
            nomeAnimal:{
                value:''
            }
        },
        formMode:FormMode.NONE
    };

    async componentDidMount(){
        const animais = await animalService.buscaTodos();
        console.log('animais',animais)
        this.setState({
            animais:animais
        });
    }

    handleSubmit = e => {
        e.preventDefault();
        this.setState({formMode:FormMode.NONE});
    }
    handleChange = e =>{
        const name = e.target.name;
        const value = e.target.value;
      
        this.setState({
          formControls: {
              ...this.state.formControls,
              [name]: {
              ...this.state.formControls[name],
              value
            }
          }
        });
    }

    handleEdit(idAnimal){
        this.setState({
            animal : this.animais.find(animal => animal.getId() == idAnimal)
        })
        //this.setState({formMode:FormMode.ADD});
    }

    render(){
        return(
            <>
                <MenuSuperior></MenuSuperior>
                <div className="container">
                    <div className="row form-group"></div>
                    <div className="d-flex justify-content-between">
                        <div>
                            <h3>Cadastro de Animal:</h3>
                        </div>
                        <div className="float-right">
                            <BotaoNovo onClick={()=>this.setState({formMode:FormMode.ADD})}/>
                        </div>
                    </div>
                    <div className="row form-group">
                        <div className="col-md-12">
                            <table className="table table-bordered table-striped">
                                <thead>
                                    <tr>
                                        <th>Nome</th>
                                        <th>Esp&eacute;cie</th>
                                        <th>Data Nascimento</th>
                                        <th>Editar/Remover</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {!this.state.animais.length ? 
                                        (<tr><td colSpan="4">Nenhum animal cadastrado</td></tr>) : 
                                        (this.state.animais.map(animal => (
                                            <tr key={animal.getId()}>
                                                <td>{animal.getNome()}</td>
                                                <td>{animal.getEspecie()}</td>
                                                <td>{animal.getDataNascimento()}</td>
                                                <td>
                                                    <MdEdit onClick={this.handleEdit.bind(this,animal.getId())} size={24}/>
                                                    <MdDelete size={24}/>
                                                </td>
                                            </tr>
                                        )))
                                        }
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <form className={this.state.formMode}>
                        <div className="row form-group">
                            <div className="col-md-6">
                                <label>Nome do Animal:</label>
                                <input
                                    type="text"
                                    name="nomeAnimal"
                                    className="form-control"
                                    value={this.state.formControls.nomeAnimal.value}
                                    onChange={this.handleChange}
                                />
                            </div>
                            <div className="col-md-3">
                                <label>Esp&eacute;cie:</label>
                                <select name="codEspecie" className="form-control">
                                    <option>Selecione...</option>
                                    <option>Coelho</option>
                                    <option>Hipop&oacute;tamo</option>
                                </select>
                            </div>
                        </div>
                        <div className="row form-group">
                            <div className="col-md-3">
                                <label>Pai:</label>
                                <select name="codAnimalPai" className="form-control">
                                    <option>Selecione...</option>
                                    <option>Gl&oacute;ria</option>
                                    <option>Vermelho</option>
                                </select>
                            </div>
                            <div className="col-md-3">
                                <label>M&atilde;e:</label>
                                <select name="codAnimalPai" className="form-control">
                                    <option>Selecione...</option>
                                    <option>Gl&oacute;ria</option>
                                    <option>Vermelho</option>
                                </select>
                            </div>
                            <div className="col-md-3">
                                <label>Data de Nascimento:</label>
                                <input type="date" name="dtNascAnimal" className="form-control"/>
                            </div>
                        </div>
                        <div className="row form-group">
                            <div className="col-md-3 col-md-offset-8">
                                <BotaoSubmit onClick={this.handleSubmit}/>
                            </div>
                        </div>
                    </form>
                </div>
            </>
        )
    }
}